.abilities-container{
    overflow-y: scroll;
    padding: 25px;
    height:500px;
    //background-color: turquoise;
}

.abilities-title{
    color: $color-4;
    text-shadow: 4px 4px 2px $color-6 ;
    margin: 10px 40px 40px 40px;
}



.bubble-container{
    display: flex;
    flex-wrap: wrap;
    

    h2{
        position:absolute;
        top:50%;
        left:50%;
        transform: translateX(-50%) translateY(-50%);
        font-family: $font-1;
        color: $color-6;
        font-size: 45px;
        text-shadow: 4px 4px 2px $color-5 ;
    }

    .big-bubble{
        background-color: $color-4;
        height: 200px;
        width: 200px;
        border-radius: 50%;
        transition : ease 0.5s;
        box-shadow: 0px 5px 7px 0px $color-5;
        margin: 15px;

        &:hover{
            transform : scale(1.20);
            filter: grayscale(80%);
        }

        h1{
            position:absolute;
            top:50%;
            left:50%;
            transform: translateX(-50%) translateY(-50%);
            font-family: $font-1;
            color: $color-6;
            font-size: 85px;
            text-shadow: 4px 4px 2px $color-5 ;
        }
    }

    .small-bubble{
        background-color: $color-4;
        height: 120px;
        width: 120px;
        position: relative;
        top: 30px;
        border-radius: 50%;
        transition : ease 0.5s;
        box-shadow: 0px 5px 7px 0px $color-5;
        margin: 25px;

        &:hover{
            transform : scale(1.20);
            filter: grayscale(80%);
        }

        h1{
            position:absolute;
            top:50%;
            left:50%;
            transform: translateX(-50%) translateY(-50%);
            font-family: $font-1;
            color: $color-6;
            font-size: 35px;
            text-shadow: 2px 2px 1px $color-5 ;
        }
    }

    .smaller-bubble{
        background-color: $color-4;
        height: 60px;
        width: 60px;
        position: relative;
        top: 30px;
        border-radius: 50%;
        transition : ease 0.5s;
        box-shadow: 0px 5px 7px 0px $color-5;
        margin: 45px;

        &:hover{
            transform : scale(1.20);
            filter: grayscale(80%);
        }

        h1{
            position:absolute;
            top:50%;
            left:50%;
            transform: translateX(-50%) translateY(-50%);
            font-family: $font-1;
            color: $color-6;
            font-size: 35px;
            text-shadow: 2px 2px 1px $color-5 ;
        }
    }
}