@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap");

$font-1: "Poppins", sans-serif;

$color-1: #ecc19c; //Fond 
$color-2: #000000; //Textes
$color-3: #1e847f;

$color-4:#408ec6;
$color-5:#1e2761;
$color-6:#7a2048;

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
    overflow:hidden;
  margin: 0.4rem auto;
  max-width: 1296px;
  height: 100vh;
  font-family: $font-1;
  color: $color-2;
  //background: linear-gradient(217deg,$color-4,$color-5 70.71%),linear-gradient(127deg,$color-5,$color-6 70.71%);   //$color-1;
  background: linear-gradient(217deg, $color-4, rgba(255,0,0,0) 50.71%),
            linear-gradient(127deg, $color-5, rgba(0,255,0,0) 65.71%),
            linear-gradient(336deg, $color-6, rgba(0,0,255,0) 100%);
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: $color-2;
  cursor: pointer;
}
a:visited {
  color: $color-2;
}

.page-title{
    text-align: center;
    color : $color-6;
}

::-webkit-scrollbar{
    visibility: hidden;
}

// Tooltip
// Tooltip container 
.tooltip {
  position: relative;
  display: inline-block;

  // Tooltip text 
  .tooltiptext {
    visibility: hidden;
    width: 180px;
    background-color: black;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
  
    position: absolute;
    bottom: 50%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
  }

  &:hover .tooltiptext {
    visibility: visible;
  }
}