.card-container{
    position: relative;
    left : 50%;
    transform: translate(-50%);
    margin: auto 0 auto 0;
    height: 530px;
    width: max-content;
    overflow-y: scroll;
}
.card-pro{
    margin: 5px auto 5px auto ;
    height: 300px;
    width: 700px;
    border: solid 5px $color-4;
    border-radius: 20px;
    box-shadow: 0px 5px 7px 0px $color-6;
    transition: ease 0.5s;

    div{
        padding-left: 10px;
    }

    .year{
        font-family: $font-1;
        font-size: 50px;
        color:white;
        text-shadow: 4px 2px 2px rgba(110, 150, 150, 0.85);
    }

    .time{
        font-family: $font-1;
        font-style: italic;
        font-size: small;
        color:white;
        text-shadow: 4px 2px 2px rgba(110, 150, 150, 0.85);
    }

    .work{
        font-family: $font-1;
        margin:10px 0 10px 0;
        font-size: 20px;
        color:$color-6;
        text-shadow: 3px 1px 2px white;
    }

    .company{
        margin:10px 0 10px 0;
        font-family: $font-1;
        font-size: larger;
        color:white;
        text-shadow: 4px 2px 2px rgba(110, 150, 150, 0.85);
    }

    .description{
        width: 70%;
        height: 100px;
        font-family: $font-1;
        font-size: 20px;
        color:white;
        text-shadow: 4px 2px 2px rgba(110, 150, 150, 0.85);
    }

    .image{
        position:relative;
        top: -235px;
        left: 490px;
        height: 190px;
        width: 190px;
        object-fit:cover;
        border-radius: 20px;
    }

    &:hover{
        transform: scale(0.95);
    }
}