.bubble-slackline{
    background: url(../../assets/Logo-Slackline.png) center/cover;
    height: 90px;
    width: 90px;
    border-radius: 50%;
    position: absolute;
    top: 200px;
    left: 200px;
    transition : ease 0.5s;

    .title{
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-40%,-50%);
        color: white;
    }

    &:hover{
        transform: scale(2);
    }
}

