.navigation {
    margin: 26px auto;
    display: flex;
    width: max-content;
    

    a {
        color: $color-5;
        margin: 10px;
        padding: 10px;
        position: relative;

        &:hover{
            top:-5px;
            }
        }
    

    .nav-active{
        &::after {
            content: '';
            width: 85%;
            height: 5px;
            background-color: $color-6;
            position: absolute;
            bottom: 1px;
            left: 50%;
            transform: translate(-50%);
        }
    }

}