.photo{
    display: flex;
    position: relative;
    background: url(../../assets/photo.png) center/cover;
    top : 50px;
    left: 50%;
    transform: translate(-50%);
    height: 500px;
    width: 500px;
    align-items: center;
    justify-content: center;
    border-radius: 250px;
    box-shadow: 0px 5px 7px 0px rgba(51, 51, 51, 0.5);
    transition : ease 0.5s;

    &:hover{
        height: 450px;
        width: 450px;
        border-radius: 225px;
        background: url(../../assets/photo_description.png) center/cover;

    }
}